import './App.css';
import {useState} from 'react';
import * as toWav from 'audiobuffer-to-wav';
import Windowing from 'fft-windowing';

const sleep = msec => new Promise(resolve => setTimeout(resolve, msec));

const audioCtx = new AudioContext({sampleRate: 48000});
const inputAudio = [];
const audioSource = [];
let syncedAudio = undefined;
let diffAudio = undefined;

function App() {
  const [offset, setOffset] = useState(4800);
  const [peakO, setPeakO] = useState(0);
  const [peakM, setPeakM] = useState(0);
  const [peakD, setPeakD] = useState(0);
  const [rmsO, setRmsO] = useState(0);
  const [rmsM, setRmsM] = useState(0);
  const [rmsD, setRmsD] = useState(0);

  async function loadFile(e) {
    // console.log('loadFile');
    let file = undefined;

    if (e.target.name === 'original') {
      file = e.target.files[0];
      console.log('loadFile', file.name);
    } else if (e.target.name === 'modified') {
      file = e.target.files[0];
      console.log('loadFile', file.name);
    } else {
      return null;
    }

    const reader = new FileReader();

    let retval = undefined;

    reader.onload = async (e) => {
      try {
        audioCtx.decodeAudioData(reader.result,
          audioBuffer => retval = audioBuffer,
          err => console.log(err)
        );
      } catch(err) {
        console.error(err);
      }
    }

    reader.readAsArrayBuffer(file);

    while (retval === undefined) await sleep(100);
    // console.log('Return');

    let mono = undefined;

    if (retval.numberOfChannels === 2) {
      mono = new AudioBuffer({
                    length: retval.length,
                    numberOfChannels: 1,
                    sampleRate: 48000,   
                 });
      const mix = mono.getChannelData(0);
      const left = retval.getChannelData(0); 
      const right = retval.getChannelData(1);

      for (let i=0; i < mono.length; i++){
        mix[i] = (left[i] + right[i])/2;
      }

    } else mono = retval;

    let peak = 0, rms = 0;
    const mix = mono.getChannelData(0);
    for (let i=0; i < mono.length; i++){
      let abs = Math.abs(mix[i]);
      rms += mix[i]*mix[i];
      if (peak < abs) peak = abs;
    } 
    rms = Math.sqrt(rms/mono.length); 

    for (let i=0; i < mono.length; i++){ /* normalize */
      mix[i] /= peak;
    }

    if (e.target.name === 'original'){ 
      setPeakO(peak); 
      setRmsO(rms);
      inputAudio[0] = mono;
    }

    if (e.target.name === 'modified'){
      setPeakM(peak);
      setRmsM(rms);
      inputAudio[1] = mono;
    }

  } // end load

  function play (e) {
    audioCtx.resume();

    if (e.target.name === 'playOriginal' && inputAudio[0]){
      console.log('play original');
      audioSource[0] = audioCtx.createBufferSource();
      audioSource[0].buffer = inputAudio[0];
      audioSource[0].connect(audioCtx.destination); 
      audioSource[0].start();
    }
    if (e.target.name === 'stopOriginal'){ 
      console.log('stop original');
      if (audioSource[0]) audioSource[0].stop();
    }

    if (e.target.name === 'playModified' && inputAudio[1]){
      console.log('play Modified');
      audioSource[1] = audioCtx.createBufferSource();
      audioSource[1].buffer = inputAudio[1];
      audioSource[1].connect(audioCtx.destination); 
      audioSource[1].start();
    }
    if (e.target.name === 'stopModified'){ 
      console.log('stop Modified');
      if (audioSource[1]) audioSource[1].stop();
    }

    if (e.target.name === 'playSynced'){
      console.log('play synced');
      if (!syncedAudio) {
        console.error('no audiobuffer');
        return;
      }
      audioSource[2] = audioCtx.createBufferSource();
      audioSource[2].buffer = syncedAudio;
      audioSource[2].connect(audioCtx.destination); 
      audioSource[2].start();
    }

    if (e.target.name === 'stopSynced'){ 
      console.log('stop Synced');
      if (!audioSource[2]) return;
      if (audioSource[2]) audioSource[2].stop();
    }

    if (e.target.name === 'playDiff' && diffAudio){ 
      console.log('playing diff');
      audioSource[2] = audioCtx.createBufferSource();
      audioSource[2].buffer = diffAudio;
      audioSource[2].connect(audioCtx.destination); 
      audioSource[2].start();
    }
    if (e.target.name === 'stopDiff'){ 
      console.log('stop diff');
      if (audioSource[2]) audioSource[2].stop();
    }

  }

  async function calcOffset(e){

    if (!inputAudio[0] || !inputAudio[1]){
      console.error('two audio files, please!!');
      return;
    } 

    /* mono tracks 48kHz */
    const original = inputAudio[0].getChannelData(0);
    const modified = inputAudio[1].getChannelData(0);
    let begin = undefined; // sec
    const duration = 1; // sec

    console.log('Find the beginning');
    for (let i = 0; i < 10*48000; i++){
      if (Math.abs(original[i]) > 10e-2){
        begin=i/48000; // in sec
        break;
      }
    }
    console.log('Begins at', begin);

    let minDiff = undefined;
    let minDiffShift = undefined;

    for (let shift = -48000; shift < 48000; shift++){
      let diffSquare = 0;

      for (let i = 48000*begin; i < 48000*(begin + duration); i++){
        if (i + shift >= 0) {
          // console.log(original[i], modified[i+shift]);
          diffSquare += Math.pow((original[i] - modified[i+shift]),2);
        }
      } 
      // console.log('shift, diffSquare', shift, diffSquare);
      // await sleep(1000);

      if (minDiff === undefined || minDiff > diffSquare){
        minDiff = diffSquare;
        minDiffShift = shift;
      }
    }

    console.log('Initial offset completed', minDiffShift);
    // setOffset(minDiffShift); // initial

// micro adjustment
    console.log('micro adjustment');
    syncedAudio = new AudioBuffer({
                    length: original.length,
                    numberOfChannels: 1,
                    sampleRate: 48000,   
                 });

    const output = syncedAudio.getChannelData(0);  
    const window = 2400;

    let lastOffset = minDiffShift; // initial offset
    let adjust = 0;
    let lastSamples = new Float32Array(window);

    for (let i=0; i < original.length; i += window/2){

      adjust = 0;
      let minDiffShift = undefined;
      let minDiff = undefined;

      for (let shift = -window/4; shift < window/4; shift++){
        let diffSquare = 0;
        let rms = 0;

        for (let j = i; j < i+ window; j++){
          rms += original[j]*original[j];
          diffSquare += Math.pow(original[j] 
            - modified[lastOffset + shift + j],2);
        } 
        rms /= Math.sqrt(rms)/window;

        if (rms > 10e-2 && 
           (!minDiff || minDiff > diffSquare)
        ){
          minDiff = diffSquare;
          minDiffShift = shift;
        }

      } // for shift

      if (minDiffShift) adjust = minDiffShift; else adjust = 0;
      // console.log('adjust', adjust);

      for (let j=0; j < window; j++) {
        if (i + j < original.length)
          lastSamples[j] = modified[lastOffset + adjust + i + j];
      }

      lastOffset += adjust;
      if (Math.abs(adjust) > 480*2) { // 480 = 10 msec
        console.log('sample#, sec, offset', i, i/48000, lastOffset);
      }
   
      const windowed = Windowing.hann(lastSamples);
      for (let k = 0; k < window; k++) output[i + k] += windowed[k]; 

    } // for i

    console.log('last offset', lastOffset);
    setOffset(lastOffset);
  }

  function calcDiff(e){
    // const begin = Math.max(0,offset);
    const begin = 0;
    const end = Math.min(inputAudio[0].length, syncedAudio.length);

    diffAudio = new AudioBuffer({
                    length: end - begin,
                    numberOfChannels: 1,
                    sampleRate: 48000,   
                });
    const orig = inputAudio[0].getChannelData(0);
    // const modified = inputAudio[1].getChannelData(0);
    const modified = syncedAudio.getChannelData(0);
    const diff = diffAudio.getChannelData(0);

    for (let i = begin; i < end; i++){
      diff[i] = orig[i] - modified[i];
    }

    let peak = 0, rms = 0;
    for (let i=0; i < diffAudio.length; i++){
      let abs = Math.abs(diff[i]);
      rms += diff[i]*diff[i];
      if (peak < abs) peak = abs;
    } 
    rms = Math.sqrt(rms/diffAudio.length); 
    setPeakD(peak);
    setRmsD(rms);

  }

  async function exportAudio(e){
    if (!diffAudio && !syncedAudio) return;

    let blob = undefined;
    let filename = undefined;
    if (e.target.name === 'exportDiff') {
      blob = await new Blob([toWav(diffAudio)], {type: 'audio/vnd.wav'});
      filename = `${Date.now()}_diff.wav`;
    } else if (e.target.name === 'exportSynced'){
      blob = await new Blob([toWav(syncedAudio)], {type: 'audio/vnd.wav'});
      filename = `${Date.now()}_synced.wav`;
    } else return;

    try {
      const saveLink = document.createElement("a");
      saveLink.href = URL.createObjectURL(blob);
      saveLink.download = filename;
      document.body.appendChild(saveLink);
      saveLink.click();
      document.body.removeChild(saveLink);
      return true;
    } catch(err) {
      console.error(err);
      return false;
    }

  }

  return (
    <div className="App">
    Original sound: <input name='original' type='file' accept='audio/*' 
         onChange={loadFile} /><br/>
    Peak: {peakO.toFixed(3)} RMS: {rmsO.toFixed(3)}<br/>
    (Normalized mono)&emsp;
    <button name='playOriginal' onClick={play}>Play</button>&emsp;
    <button name='stopOriginal' onClick={play}>Stop</button><br/>
    <hr/>
    Modified sound: <input name='modified' type='file' accept='audio/*' 
         onChange={loadFile} /><br/>
    Peak: {peakM.toFixed(3)} RMS: {rmsM.toFixed(3)}<br/>
    (Normalized mono)&emsp;
    <button name='playModified' onClick={play}>Play</button>&emsp;
    <button name='stopModified' onClick={play}>Stop</button><br/>
    <hr/>
    Calculate global offset: &emsp;
    <button name='offset' onClick={calcOffset}>CalcOffset</button>&emsp;<br/>
    Offset: {offset} (samples), &emsp; {(offset/48).toFixed(3)} (m sec)
    <hr/> 
    Synced Audio (w/ micro offset adjustment): &emsp;
    <button name='playSynced' onClick={play}>Play</button>&emsp;
    <button name='stopSynced' onClick={play}>Stop</button>&emsp;
    <button name='exportSynced' onClick={exportAudio}>Export</button>
    <hr/>
    Calculate diff: &emsp; 
    <button name='calcDiff' onClick={calcDiff}>
       Calculate diff</button>&emsp;<br/>
    Peak: {peakD.toFixed(3)} RMS: {rmsD.toFixed(3)}<br/>
    <button name='playDiff' onClick={play}>Play</button>&emsp;
    <button name='stopDiff' onClick={play}>Stop</button>&emsp;
    <button name='exportDiff' onClick={exportAudio}>Export</button>
    <hr/>
    </div>
  );
}

export default App;
